/* global console, document, Excel, Office, setInterval */
import "whatwg-fetch";
import globalVar from "./globalVars.js";
import {
  filterCards,
  setupEventListeners,
  handleWorksheetActivation,
  addTrackingNumbers,
  getTableHeaders,
  getMasterTableValues,
  getLetterByIndex,
  activateEvents,
  // updateNotes,
} from "./functions.js";

// #endregion ----------------------------------------------------------------------------------------------------------

/**
 * Runs when the taskpane is opened.
 */
Office.onReady(async (info) => {
  if (info.host === Office.HostType.Excel) {
    await Excel.run(async (context) => {
      document.getElementById("loading").style.display = "none";

      const fileArr = Office.context.document.url.split("/"); // Full URL of the document, split by slashes.
      const fileName = fileArr[fileArr.length - 1].split(".")[0].slice(-1).toUpperCase(); // The last bit of the array, which will be document.xlsx. Then split that by the period, and assign it the filename minus the extension. Then grab the last letter and capitalise.

      document.querySelector("#grouplett").innerHTML = fileName;

      activateEvents();

      // Setup event listeners
      await setupEventListeners(context);

      // Initial call to handle the current worksheet
      await handleWorksheetActivation();

      await addTrackingNumbers();

      // Let's get the column numbers.
      let sheet = context.workbook.worksheets.getItem("Master");
      const headers = await getTableHeaders("Master");

      setInterval(async function () {
        await addTrackingNumbers();

        const headers = await getTableHeaders("Master");
        const tableData = await getMasterTableValues(headers);

        const notesMap = tableData.reduce((acc, row) => {
          acc[row[headers.indexOf("UJID")]] = row[headers.indexOf("Notes")];
          return acc;
        }, {});

        const cards = document.querySelectorAll(".card");
        cards.forEach((card) => {
          const noteObj = notesMap[card.dataset.ujid];
          const textarea = card.querySelector(".prodnotes");

          textarea.disabled = false;
          textarea.value = noteObj;
          textarea.disabled = true;
        });
      }, globalVar.minute * 5);

      const columnsToFormat = ["Operator", "Line No", "Stop", "Start", "Operator Notes", "Tracking No"];

      // Loop through each column and apply formatting
      for (const header of columnsToFormat) {
        const columnIndex = headers.indexOf(header);
        if (columnIndex !== -1) {
          const columnLetter = getLetterByIndex(columnIndex);
          const range = sheet.getRange(`${columnLetter}:${columnLetter}`);
          range.format.fill.color = "white";
          range.format.font.color = "black";
          range.format.font.bold = false;
        }
      }

      await context.sync();
      filterCards();

      // -------------------------------------------------------------------- //

    });
  }
});

/**
 * Add event listeners for both keyup and input events
 */
document.getElementById("filterInput").addEventListener("keyup", filterCards);
document.getElementById("filterInput").addEventListener("input", filterCards);
document.getElementById("filter").addEventListener("change", filterCards);
document.getElementById("products").addEventListener("change", filterCards);
document.getElementById("lines").addEventListener("change", filterCards);


document.getElementById("closemodal").addEventListener("click", function () {
  document.querySelector("#modal-background").style.display = "none";
  document.querySelector("#modal").style.display = "none";
});
document.getElementById("help").addEventListener("click", function () {
  document.querySelector("#modal-background").style.display = "flex";
  document.querySelector("#modal").style.display = "block";
  document.querySelector("#modal-help").style.display= "block";
});

